import { Component,ViewChild, ElementRef  } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'fullPage';
  @ViewChild('h1')
  h1: ElementRef;
  @ViewChild('p1')
  p1: ElementRef;
  @ViewChild('div1')
  div1: ElementRef;
  onClickh1(): void {
    this.h1.nativeElement.scrollIntoView({behavior: 'smooth'}); 
  }
  onClickp1(): void {
    this.p1.nativeElement.scrollIntoView({behavior: 'smooth'}); 
  }
  onClickdiv1(): void {
    this.div1.nativeElement.scrollIntoView({behavior: 'smooth'}); 
  }
}
  
 